.chat_box {
	width: calc(70vw - 20px);
	height: calc(100vh - 110px);
	background-color: var(--primary-white);
	border-radius: 5px;
	padding: 20px;
}

.chat_box_full {
	width: 100%;
	height: calc(100vh - 110px);
	background-color: var(--primary-white);
	border-radius: 5px;
	padding: 20px;
}
