.navbar {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: 20px;
	height: 60px;
	width: 100vw;
	padding: 0px 25px 0px 15px;
	background-color: var(--primary-white);
}

.title {
	font-family: "Montserrat";
	font-weight: 600;
	font-size: 25px;
	text-shadow: 0px 0px 1.5px var(--primary-black);
}

.search_bar {
	position: relative;
	display: flex;
	align-items: center;
	gap: 15px;
	width: 45%;
	height: 35px;
	padding: 0px 15px;
	background-color: var(--primary-white);
	box-shadow: 0px 0px 3px var(--primary-black);
	border-radius: 20px;
}

.search_icon {
	border: none;
	outline: none;
	background-color: transparent;
	height: 25px;
	width: 25px;
	cursor: pointer;
}

.search_input {
	display: flex;
	background-color: var(--primary-white);
	width: 100%;
	height: 100%;
	font-family: "Montserrat";
	font-size: 16px;
	border: none;
}

.search_input::placeholder {
	font-family: "Montserrat";
	font-size: 16px;
	margin: none;
	padding: none;
}

.search_input:focus {
	outline: none;
}

.user_features {
	position: relative;
	display: flex;
	align-items: center;
	gap: 25px;
}

.friends_icon {
	border: none;
	outline: none;
	background-color: transparent;
	height: 30px;
	width: 30px;
	cursor: pointer;
}

.friends_alert {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 12.5px;
	height: 12.5px;
	background-color: var(--primary-orange);
	border-radius: 50%;
	top: 19.5%;
	right: 74.85%;
}

.friends_alert_number {
	font-family: "Montserrat";
	font-weight: 600;
	font-size: 8px;
	color: var(--primary-white);
}

.notifications_icon {
	border: none;
	outline: none;
	background-color: transparent;
	height: 30px;
	width: 30px;
	cursor: pointer;
}

.notifications_alert {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 12.5px;
	height: 12.5px;
	background-color: var(--primary-orange);
	border-radius: 50%;
	top: 19.5%;
	right: 43.5%;
}

.notifications_alert_number  {
	font-family: "Montserrat";
	font-weight: 600;
	font-size: 8px;
	color: var(--primary-white);
}

.profile_pic_btn {
	border: none;
	outline: none;
	background-color: transparent;
}

.profile_pic {
	border: 1px solid rgba(0, 0, 0, 0.25);
	padding: 3px;
	border-radius: 100%;
	box-shadow: 0px 0px 3px var(--primary-black);
	width: 40px;
	height: 40px;
	overflow: hidden;
	cursor: pointer;
}

@media screen and (max-width: 600px) {
	.navbar {
		padding: 0px 10px 0px 5px;
	}

	.search_bar {
		width: 80%;
	}
}

@media screen and (max-width: 475px) {
	.title {
		font-size: 20px;
	}

	.user_features {
		gap: 20px;
	}

	.search_icon {
		height: 21px;
		width: 21px;
	}

	.friends_icon {
		height: 25px;
		width: 25px;
	}

	.notifications_icon {
		height: 25px;
		width: 25px;
	}
}
