.modal {
	position: fixed;
	display: flex;
	align-items: center;
	justify-content: center;
	top: 0;
	right: 0;
	width: 100vw;
	height: 100vh;
	background-color: var(--modal-background);
	z-index: 1;
}

.settings_modal {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 500px;
	background-color: var(--primary-white);
	padding: 20px 50px;
	border-radius: 20px;
	box-shadow: 0px 0px 5px var(--primary-white);
}

.close_button {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 25px;
	width: 25px;
	right: 20px;
	border: none;
	outline: none;
	background-color: var(--primary-white);
	border-radius: 5px;
	cursor: pointer;
	transition: all 0.2s ease;
}

.close_button:hover {
	background-color: var(--primary-orange);
}

.chat_name {
	font-family: "Montserrat";
	font-size: 25px;
	margin-top: 20px;
	text-align: center;
}

.input_section {
	display: flex;
	flex-direction: column;
	gap: 15px;
	margin-top: 20px;
}

.rename_button {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 28.4px;
	width: 28.5px;
	right: 1px;
	top: 44.89%;
	border: none;
	outline: none;
	background-color: var(--primary-orange);
	color: var(--primary-white);
	border-radius: 5px;
	cursor: pointer;
	transition: all 0.2s ease;
}

.rename_button:hover {
	color: var(--primary-black);
}

.input_container {
	position: relative;
	display: flex;
	flex-direction: column;
	gap: 5px;
}

.label {
	font-family: "Montserrat";
	font-size: 14px;
	color: grey;
}

.input {
	height: 30px;
	width: 350px;
	font-family: "Montserrat";
	border-radius: 6px;
	border: 1px solid var(--primary-black);
	background-color: var(--primary-white);
	padding: 10px;
}

.triangle_icon {
	position: absolute;
	top: 57.5%;
	right: 2.5%;
	cursor: pointer;
}

.triangle_icon:hover {
	color: var(--primary-orange);
}

.create_btn {
	display: flex;
	justify-content: center;
	align-items: center;
	color: #f5f5f5;
	background-color: #171717;
	border-radius: 20px;
	font-family: "Montserrat";
	font-weight: 600;
	font-size: 16px;
	text-align: center;
	margin: 50px auto 0px auto;
	height: 40px;
	width: 150px;
	border: none;
	padding: 10px;
	cursor: pointer;
	transition-duration: 0.5s;
}

.search_drawer {
	display: flex;
	flex-direction: column;
	width: 340px;
	min-height: 30px;
	max-height: 200px;
	gap: 10px;
	margin-top: -15px;
	margin-left: 5px;
	overflow: auto;
	padding: 10px 5px;
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
	background-color: var(--primary-white);
	border-top: none;
	box-shadow: 0px 0px 2.5px var(--primary-black);
}

.loading_container {
	margin: 0 auto;
}

.no_results {
	font-family: "Montserrat";
	font-size: 15px;
	font-weight: 400;
	text-align: center;
}

.user_container {
	display: flex;
	align-items: center;
	gap: 10px;
	width: 100%;
	height: 50px;
	border-radius: 20px;
	cursor: pointer;
}

.user_container:hover {
	background-color: var(--primary-orange-faded);
}

.user_container_selected {
	display: flex;
	align-items: center;
	gap: 10px;
	width: 100%;
	border-radius: 20px;
	cursor: pointer;
	background-color: var(--primary-orange);
}

.profile_pic {
	border: 1px solid rgba(0, 0, 0, 0.25);
	width: 40px;
	height: 40px;
	margin-left: 10px;
	padding: 3px;
	border-radius: 100%;
	box-shadow: 0px 0px 3px var(--primary-black);
	overflow: hidden;
}

.user__info_container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	gap: 2px;
}

.user_name {
	font-family: "Montserrat";
	font-size: 14px;
	font-weight: 600;
}

.email {
	font-family: "Montserrat";
	font-size: 10px;
	font-weight: 400;
}

.selected_user_container {
	display: flex;
	gap: 15px;
	width: 340px;
	max-height: 300px;
	overflow: auto;
	margin: 0px 0px 7.5px 5px;
}

.selected_user {
	display: flex;
	align-items: center;
	gap: 10px;
	background-color: var(--primary-orange);
	border-radius: 5px;
	padding: 5px 10px 5px 10px;
}

.profile_pic_selected {
	border: 1px solid rgba(0, 0, 0, 0.25);
	width: 25px;
	height: 25px;
	padding: 3px;
	border-radius: 100%;
	box-shadow: 0px 0px 3px var(--primary-black);
	overflow: hidden;
}

.user_name_selected {
	font-family: "Montserrat";
	font-weight: 400;
	font-size: 14px;
	color: var(--primary-black);
}

.close_button_selected {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 15px;
	width: 15px;
	border: none;
	outline: none;
	color: white;
	background-color: var(--primary-orange);
	cursor: pointer;
	transition: all 0.2s ease;
}

.leave_group {
	display: flex;
	justify-content: center;
	align-items: center;
	color: #f5f5f5;
	background-color: #171717;
	border-radius: 20px;
	font-family: "Montserrat";
	font-weight: 600;
	font-size: 16px;
	text-align: center;
	margin: 50px auto 0px auto;
	height: 40px;
	width: 150px;
	border: none;
	padding: 10px;
	cursor: pointer;
	transition-duration: 0.5s;
}

.leave_group:hover {
	background-color: var(--primary-red);
}

@media screen and (max-width: 600px) {
	.settings_modal {
		width: fit-content;
		padding: 30px 15px;
	}

	.input {
		width: 250px;
		margin: 0 auto;
	}

	.label {
		margin-left: 50px;
	}

	.rename_button {
		right: 14%;
	}

	.triangle_icon {
		right: 17%;
	}

	.selected_user_container {
		max-width: 340px;
		height: fit-content;
	}

	.search_drawer {
		margin-top: -10px;
		width: 240px;
		margin: -15px auto 0px auto;
	}

	.leave_group {
		margin-top: 30px;
	}
}
